<template>
  <button @click="$emit('onClick')" :class="color" :type="type" :name="name">
    <span v-if="icon"><font-awesome-icon :icon="icon" size="lg" /></span>
    {{ text }}
  </button>
</template>

<script>
export default {
  name: "Button",
  props: {
    text: String,
    type: { type: String, default: "submit" },
    icon: { type: String },
    color: { type: String, default: "green" },
    name: String,
  },
};
</script>

<style lang="scss" scoped>
button {
  width: 100%;
  color: white;
  padding: 10px 16px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 700;
  &:active {
    filter: brightness(70%);
  }
  &:hover {
    filter: brightness(80%);
  }
}
.green {
  background-color: $secondary;
}
.red {
  background-color: $danger;
}
.flat {
  background-color: $bg-dark;
}
</style>