<template>
  <Window>
    <template v-slot:component>
      <h3>{{ text.messages.loggedOut }}</h3>
    </template>
  </Window>
</template>

<script>
import Window from "../../components/window.vue";
import Ar from "../../lang/ar";
export default {
  name: "Logout",
  components: { Window },
  data() {
    return {
      text: Ar,
    };
  },
  mounted() {
    this.$store.dispatch("act_logout");
    this.$router.push({ redirect: { name: "Home" } });
  },
};
</script>

