import Login from "../../views/auth/Login.vue";
import Welcome from "../../views/auth/Welcome.vue";
import Logout from "../../views/auth/Logout.vue";

const auth = [
  {
    path: "/login",
    name: "Login",
    component: Login,
  },
  {
    path: "/welcome",
    name: "Welcome",
    component: Welcome,
    meta: {
      requiresAuth: true,
    },
  },

  {
    path: "/logout",
    name: "Logout",
    component: Logout,
    meta: {
      requiresAuth: true,
    },
  },
];

export default auth;
