<template>
  <header>
    <router-link :to="{ name: 'Home' }"
      ><img src="../assets/logo.png"
    /></router-link>
    <h4 v-if="isAuthenticated && loading === false">
      {{ this.text.messages.welcome }}
      <Dropdown :list="dropdownItems" v-if="isAuthenticated"
        ><template v-slot:button>
          <span class="username">
            {{
              this.user.fullName || this.user.username || this.user.email
            }}</span
          ></template
        ></Dropdown
      >
    </h4>
    <h4 v-else-if="loading === false">Login</h4>
  </header>

  <add-contact
    :customClass="[showAddWindow ? 'showUpEffect' : '']"
    @closeWin="closeWin"
    @closeWinDb="closeWin"
    v-if="showAddWindow"
  />
</template>

<script>
import { mapGetters } from "vuex";
import Ar from "../lang/ar";
import AddContact from "./AddContactForm.vue";
import Dropdown from "./Dropdown.vue";

const dropDownItems = [
  { id: 1, text: "Profile", link: "profile" },
  {
    id: 2,
    text: "Add contact",
    link: "AddContact",
    icon: "plus-circle",
  },
  {
    id: 3,
    text: "Logout",
    link: "Logout",
    icon: "sign-out-alt",
  },
];

export default {
  components: { AddContact, Dropdown },
  name: "Header",
  data() {
    return {
      text: Ar,
      showAddWindow: false,
      dropdownItems: dropDownItems,
      processing: false,
    };
  },
  computed: { ...mapGetters(["user", "isAuthenticated", "loading"]) },
};
</script>

<style lang="scss" scoped>
header {
  @include flexed(row, space-between, center);
  @media only screen and (max-width: $small-screen) {
    @include flexed(column, space-between, center);
  }
  margin: 20px auto 8vh auto;
  min-height: 12vh;
  width: 100%;
  .username {
    cursor: pointer;
    color: $text_dark;
    &:hover {
      color: $primary;
    }
  }
}

img {
}
</style>
