import axios from "axios";

import tokensControl from "../../helpers/tokensControl";

const state = {
  user: {},
  isAuthenticated: false,
  loading: false,
};

const getters = {
  user: (state) => state.user,
  isAuthenticated: (state) => state.isAuthenticated || tokensControl.check(),
  loading: (state) => state.loading,
};

const actions = {
  async act_login({ commit }, loginInformation) {
    try {
      const result = await axios.post(
        `${process.env.VUE_APP_SERVICE_URL}/auth/signin`,
        loginInformation
      );
      const userData = result.data;
      userData.fullName = userData.firstName?`${userData.firstName} ${userData.lastName}`:userData.email ;
      tokensControl.set(userData.tokens);
      commit("login", userData);
      return result.status;
    } catch (error) {
      return error.response.status;
    }
  },

  // LOGOUT
  act_logout({ commit }) {
    tokensControl.remove();
    commit("logout");
  },

  // CHECK AUTH
  async checkAuth({ commit, dispatch }) {
    commit("loading", true);
    try {
      state.loading = true;
      if (!getters.isAuthenticated) {
        throw "err";
      }

      const result = await axios.get(
        `${process.env.VUE_APP_SERVICE_URL}/auth/auth`,
        tokensControl.httpAuthorization.withAccessToken()
      );
      const userData = result.data;

      userData.fullName = userData.firstName?`${userData.firstName} ${userData.lastName}`:userData.email ;
      commit("checkAuth", userData);

      return result.status;
    } catch (error) {
      if (error.response.status === 401) {
        await dispatch("refreshToken");
      } else {
        dispatch("act_logout");
      }
      return error;
    } finally {
      commit("loading", false);
    }
  },
  // CHECK AUTH
  async refreshToken({ commit, dispatch }) {
    if (!tokensControl.check()) throw "error";

    try {
      const result = await axios.get(
        `${process.env.VUE_APP_SERVICE_URL}/auth/re-access`,
        tokensControl.httpAuthorization.withRefreshToken()
      );

      const userData = await result.data;

      userData.fullName = userData.firstName?`${userData.firstName} ${userData.lastName}`:userData.email ;

      // Replace the tokens
      tokensControl.replace(userData.tokens);

      commit("refreshToken", userData);

      return result.status;
    } catch (error) {
      dispatch("act_logout");

      throw error;
    }
  },
};

const mutations = {
  loading: (state, data) => (state.loading = data),
  login: (state, data) => {
    data.tokens = undefined;
    state.user = data;
    state.isAuthenticated = true;
  },
  logout: (state) => {
    state.user = {};
    state.isAuthenticated = false;
  },
  checkAuth: (state, data) => {
    state.user = data;
    state.isAuthenticated = true;
  },
  refreshToken: (state, data) => {
    state.user = data;
  },
};

export default {
  state,
  getters,
  actions,
  mutations,
};
