import Profile from "../../views/profile/Profile.vue";
import ChangePassword from "../../views/profile/ChangePassword.vue";

const profile = [
  {
    path: "/profile",
    name: "profile",
    component: Profile,
  },
  {
    path: "/profile/changePassword",
    name: "changePassword",
    component: ChangePassword,
  },
];

export default profile;
