<template>
  <h2>
    {{ text.windows.changePassword }}
  </h2>
  <Window>
    <template v-slot:component>
      <form @submit="saveNewPassword">
        <Input
          v-model="oldPassword"
          :label="text.windows.oldPassword"
          :edit="0"
          :required="true"
        />
        <Input
          v-model="newPassword"
          :label="text.windows.newPassword"
          :edit="0"
          :required="true"
        />
        <Input
          v-model="newPasswordConfirm"
          :label="text.windows.newPasswordConfirm"
          :edit="0"
          :required="true"
        />
        <Button type="submit" :text="text.windows.save" />
      </form>
    </template>
  </Window>
</template>

<script>
import { mapActions } from "vuex";

import Button from "../../components/Button.vue";
import Input from "../../components/Input.vue";
import Window from "../../components/window.vue";
import Ar from "../../lang/ar";
export default {
  components: { Input, Button, Window },
  name,
  Window: "ChangePassword",
  data() {
    return {
      text: Ar,
      oldPassword: "",
      newPassword: "",
      newPasswordConfirm: "",
    };
  },
  methods: {
    ...mapActions(["changePassword"]),
    async saveNewPassword(e) {
      e.preventDefault();

      const passwordFields = {
        oldPassword: this.oldPassword,
        newPassword: this.newPassword,
        newPasswordConfirm: this.newPasswordConfirm,
      };

      if (passwordFields.oldPassword === passwordFields.newPassword)
        return this.$toast.error(this.text.messages.oldPasswordSameNew);

      if (passwordFields.newPassword != passwordFields.newPasswordConfirm)
        return this.$toast.error(this.text.messages.newPasswordConfirmNotSame);

      const passwordIsChanged = await this.changePassword(passwordFields);
      if (passwordIsChanged[0]) {
        this.$toast.success("tamam");
        this.$router.push({ name: "profile" });
      } else {
        if (passwordIsChanged[1] === 401) {
          this.$toast.error(this.text.messages.passwordError);
        } else {
          this.$toast.error(this.text.messages.error);
        }
      }
    },
  },
};
</script>

<style lang="scss" scoped>
</style>