<template>
  <div class="dropdown">
    <slot name="button"></slot>
    <div class="dropdown-contents">
      <div class="dropdown-item" :key="item.id" v-for="item in list">
        <router-link :to="{ name: item.link }">
          <div class="item-contents">
            {{ item.text }} <Icon v-if="item.icon" :icon="item.icon" /></div
        ></router-link>
      </div>
    </div>
  </div>
</template>

<script>
import Icon from "./Icon.vue";

export default {
  components: { Icon },
  name: "Dropdown",
  componants: {
    Icon,
  },
  props: {
    list: Array,
  },
};
</script>

<style lang="scss" scoped>
.dropdown {
  position: relative;
  display: inline-block;
  &:hover .dropdown-contents {
    display: block;
  }
  .dropdown-contents {
    background-color: $bg-dark;
    position: absolute;
    left: 0;
    width: 150px;
    padding: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.514);
    border-radius: 4px;
    overflow: hidden;
    filter: brightness(130%);
    display: none;

    .dropdown-item {
      cursor: pointer;
      background-color: $bg_dark;
      font-size: 1rem;
      transition: 0.3s ease-in-out;
      a {
        text-decoration: none;
      }
      .item-contents {
        padding: 3px 6px;
        color: $text_dark;
        @include flexed(row, space-between, center);
      }

      &:hover {
        filter: brightness(130%);
        padding-right: 10px;
        color: $text-light;
      }
    }
  }
}
</style>