import Add from "../../views/contacts/Add.vue";
import Search from "../../views/contacts/Search.vue";
import NotFound from "../../views/errors/NotFound.vue";

const contacts = [
  {
    path: "/contacts/add",
    name: "AddContact",
    component: Add,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/search",
    name: "Search",
    component: Search,
    props: (route) => ({ routeValue: route.query.q }),
    meta: {
      requiresAuth: true,
    },
  },
  { path: "/:catchAll(.*)", component: NotFound },
];

export default contacts;
