import axios from "axios";

import tokensControl from "../../helpers/tokensControl";

// Headers Options

const state = {
  contacts: [],
};

const getters = {
  allContacts: (state) => state.contacts,
};

const actions = {
  //
  //
  // ADD CONTACT

  async act_addContact({ commit, dispatch }, contact) {
    try {
      await dispatch("checkAuth");
      const response = await axios.post(
        `${process.env.VUE_APP_SERVICE_URL}/contacts/add`,
        contact,
        tokensControl.httpAuthorization.withAccessToken()
      );
      console.log("from action", response.data);
      commit("addContact", response.data);
    } catch (error) {
      throw { error };
    }
  },

  //
  //
  // ----- SEARCH FOR CONTACTS  ------//

  async act_findContacts({ commit, dispatch }, data) {
    try {
      const { value, page, limit } = data;
      await dispatch("checkAuth");
      const response = await axios.get(
        `${process.env.VUE_APP_SERVICE_URL}/contacts/?_search=${value}&_page=${page}&_limit=${limit}`,
        tokensControl.httpAuthorization.withAccessToken()
      );

      commit("findContacts", response.data);
    } catch (error) {
      throw { error };
    }
  },

  //
  //
  // EXTEND SEARCHING RESULTS

  async act_extendResults({ commit }, data) {
    try {
      const { value, page, limit } = data;

      const response = await axios.get(
        `${process.env.VUE_APP_SERVICE_URL}/contacts?_search=${value}&_page=${page}&_limit=${limit}`,
        tokensControl.httpAuthorization.withAccessToken()
      );

      if (!response.data.length) {
        return false;
      }

      commit("extendResults", response.data);

      return true;
    } catch (error) {
      return false;
    }
  },

  //

  //

  // EDIT A CONTACT ACTION

  async act_editContact({ commit }, contact) {
    try {
      const response = await axios.put(
        `${process.env.VUE_APP_SERVICE_URL}/contacts/${contact._id}/edit`,
        contact,
        tokensControl.httpAuthorization.withAccessToken()
      );

      commit("editContact", response.data);
    } catch (error) {
      console.log(error);
    }
  },

  //
  // DELETE A CONTACT //

  async act_deleteContact({ commit }, contact) {
    await axios.delete(
      `${process.env.VUE_APP_SERVICE_URL}/contacts/${contact._id}/delete`,
      tokensControl.httpAuthorization.withAccessToken()
    );
    commit("deleteContact", contact);
  },
};

const mutations = {
  addContact: (state, contact) => state.contacts.unshift(contact),

  findContacts: (state, contact) => (state.contacts = contact),

  extendResults: (state, contact) =>
    (state.contacts = state.contacts.concat(contact)),

  editContact: (state, contact) => {
    const index = state.contacts.findIndex((con) => con._id === contact._id);

    if (index !== -1) {
      state.contacts.splice(index, 1, contact);
    }
  },

  deleteContact: (state, contact) =>
    (state.contacts = state.contacts.filter(
      (cont) => cont._id !== contact._id
    )),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
