<template>
  <Window>
    <template v-slot:component>
      <h3>{{ title }}</h3>
      <form @submit.prevent="$emit('user', user)">
        <Input
          name="username"
          v-model="user.username"
          :label="text.user.username"
          :placeholder="text.user.username"
          required
        />
        <Input
          name="password"
          v-model="user.password"
          :label="text.user.password"
          type="password"
          :placeholder="text.user.password"
          required
        />
        <Button type="submit" :text="text.windows.titles.login" />
      </form>
      <Spinner v-show="proccessing" />
    </template>
  </Window>
</template>

<script>
import Button from "../Button.vue";
import Input from "../Input.vue";
import Spinner from "../Spinner.vue";
import Ar from "../../lang/ar";
import Window from "../window.vue";

export default {
  name: "UserLogForm",
  data() {
    return {
      text: Ar,
      user: {
        username: "",
        password: "",
      },
    };
  },
  props: {
    proccessing: Boolean,
    title: String,
  },
  components: {
    Input,
    Button,
    Spinner,
    Window,
  },
};
</script>

