<template>
  <popup-window :title="ar.windows.edit_contact">
    <template v-slot:topBarBtns>
      <Icon
        class="icon"
        v-show="!disabled"
        :title="ar.windows.undo"
        icon="undo"
        @click="clickCancel"
      />
      <Icon
        class="icon"
        v-show="disabled"
        :title="ar.windows.edit_contact"
        icon="user-edit"
        @click="enableEdit"
      />
    </template>
    <template v-slot:component>
      <!-- Component -->
      <contact-fields
        :title="ar.windows.titles.contact_info"
        type="view"
        :disabled="disabled"
        :contact="contact"
        :name="name"
        @onSubmit="onSubmit"
        @reset="handleCancelFun"
      >
        <template v-slot:submitters>
          <Spinner v-show="savingProcess" />
          <div class="btnsBar">
            <Button
              v-show="!disabled && !savingProcess"
              color="green"
              :text="ar.windows.save"
              :title="ar.windows.save"
              name="save"
            ></Button>
            <Button
              v-show="!disabled && !savingProcess"
              color="red"
              icon="trash"
              :title="text.windows.delete"
              name="delete"
            ></Button>
          </div>
        </template>
      </contact-fields>
      <!-- /Component -->
    </template>
  </popup-window>
</template>

<script>
import ContactFields from "../../components/ContactFields.vue";
import Button from "../../components/Button.vue";
import ar from "../../lang/ar";
import Spinner from "../../components/Spinner.vue";
import { mapActions } from "vuex";
import PopupWindow from "../../components/PopupWindow.vue";
import Icon from "../../components/Icon.vue";
export default {
  name: "Review",
  props: {
    contact: Object,
    name: String,
  },
  data() {
    return {
      ar,
      disabled: 1,
      savingProcess: false,
      text: ar,
      reset: function () {},
    };
  },
  components: {
    ContactFields,
    Button,
    Spinner,
    PopupWindow,
    Icon,
  },

  methods: {
    ...mapActions(["act_editContact", "act_deleteContact"]),
    enableEdit() {
      this.disabled = 0;
    },
    disableEdit() {
      this.disabled = 1;
    },
    handleCancelFun(e) {
      this.reset = e;
    },
    clickCancel() {
      this.reset();
      this.disableEdit();
    },
    onSubmit(e) {
      if (e.submitter === "save") {
        this.save(e.updContact);
      } else if (e.submitter === "delete") {
        this.delete(e.updContact);
      }
    },
    save(contact) {
      this.savingProcess = true;
      console.log(contact);
      this.act_editContact(contact)
        .then(() => {
          this.savingProcess = false;
          this.$toast.success(this.text.messages.edit_success);
          this.disabled = 1;
        })
        .catch((e) => {
          this.$toast.error(this.text.messages.error);
          console.error(e.message);
        });
    },
    delete(contact) {
      this.savingProcess = true;
      this.act_deleteContact(contact)
        .then(() => {
          this.savingProcess = false;
          this.$toast.success(this.text.messages.delete_success);

          this.$emit("closeWin", false);
        })
        .catch((e) => {
          this.$toast.error(this.text.messages.error);
          console.error(e.message);
        });
    },
  },
};
</script>

<style lang='scss' scoped>
.icon {
  cursor: pointer;
  color: $text-dark;
  transition: all 0.5s ease-in;
  &:hover {
    filter: brightness(80%);
  }
}

.btnsBar {
  @include flexed(row, space-between, center);
  button:last-child {
    width: 50px;
    margin-right: 10px;
  }
  button:first-child {
    width: 100%;
  }
}
</style>