<template>
  <div class="background" @dblclick="$emit('closeWinDb', $event)">
    <div class="window showUpEffect">
      <div class="window-body">
        <div class="window-head">
          <h3>
            {{ title }}
            <slot name="topBarBtns"></slot>
          </h3>
        </div>
        <slot name="component"></slot>
        <slot name="bottomBarBtns"></slot>
        <Button
          color="flat"
          type="button"
          :text="text.windows.close"
          :title="text.windows.close"
          @click="$emit('closeWin', false)"
        ></Button>
      </div>
    </div>
  </div>
</template>


<script>
import Button from "./Button.vue";
import ar from "../lang/ar";

export default {
  name: "PopupWindow",
  props: {
    contact: Object,
    name: String,
    title: String,
    disabled: Number,
  },
  data() {
    return {
      text: ar,
    };
  },
  components: {
    Button,
  },
};
</script>

<style lang='scss' scoped>
.background {
  position: fixed;
  top: 0;
  bottom: 0;
  height: 100%;
  width: 100%;

  background-color: #0000005b;
  @include flexed(column, center, center);
  .window {
    padding: 10px;
    width: 92vw;
    background-color: #112d42;
    border-radius: 10px;
    position: relative;
    text-align: right;

    @include flexed(column, space-between, center);
    .window-body {
      margin-bottom: 10px;
      width: 90%;
      .window-head {
        h3 {
          width: 30%;
        }
      }
    }
  }
  .showUpEffect {
    animation: showMove 0.5s ease-out;
  }

  .profilePhoto {
    background-color: $bg-light;
    border-radius: 5px;
    max-width: 190px;
  }
}

@keyframes showMove {
  from {
    transform: scale(40%);
  }
  to {
    transform: scale(100%);
  }
}
</style>