<template>
  <div class="search-page">
    <SearchBox @search="onSubmit" v-if="!savingProcess" />
    <div class="resultsTitle">
      <h3>{{ text.messages.results }}</h3>
      <hr />
    </div>
    <Spinner v-show="savingProcess" />
    <Results
      v-if="!savingProcess"
      :contacts="allContacts"
      :thereMoreResults="thereMoreResults"
      @extendTheResults="extendSearchResults"
    />
  </div>
</template>

<script>
//modules
import { mapActions, mapGetters } from "vuex";

//components
import SearchBox from "../../components/SearchBox.vue";
import Results from "./Results.vue";
import Spinner from "../../components/Spinner.vue";

//helpers
import Ar from "../../lang/ar";

//methods

/* 
    
    find results by router query
   
   */
function load(components) {
  components.page = 1;
  findContact(components);
}

/* 
    
    find contacts by send it to dispatch
   
   */

async function findContact(component) {
  component.savingProcess = true;
  try {
    const searchInformation = {
      page: component.page,
      limit: component.limit,
      value: component.submittedValue || component.routeValue,
    };
    component.searchValues = searchInformation;

    await component.act_findContacts(component.searchValues);

    moreResultsTaggle(component, true);
    component.savingProcess = false;
  } catch (error) {
    console.log(error);
    component.savingProcess = false;
  }
}

/*


check if there any more results and show button if there any amount of result


*/
function moreResultsTaggle(components, state) {
  if (components.limit > components.allContacts.length) {
    components.thereMoreResults = false;
  } else {
    components.thereMoreResults = state;
  }
}

export default {
  name: "Search",
  components: {
    SearchBox,
    Results,
    Spinner,
  },
  props: {
    routeValue: {
      type: String,
    },
  },
  data() {
    return {
      text: Ar,
      searchValues: {},
      submittedValue: "",
      thereMoreResults: true,
      savingProcess: true,
      limit: 8,
      page: 1,
    };
  },

  computed: {
    ...mapGetters(["allContacts"]),
  },

  async created() {
    load(this);
  },

  methods: {
    ...mapActions(["act_findContacts", "act_extendResults"]),

    async onSubmit(value) {
      this.submittedValue = value;
      load(this);
    },

    /* 

Pagination 
extend results counts

*/
    async extendSearchResults() {
      try {
        this.page++;
        this.searchValues.page = this.page;
        //for store state boolean and show and hide elements
        moreResultsTaggle(
          this,
          await this.act_extendResults(this.searchValues)
        );
      } catch (error) {
        moreResultsTaggle(this, false);
        console.log(error);
      }
    },
  },
};
</script>



<style lang="scss" scoped>
.search-page {
  width: 100%;
  @include flexed(column, space-between, center);
  .resultsTitle {
    color: $text_dark;
    width: 100%;
    hr {
      border: 1px solid $text-dark;
    }
  }
}
</style>