<template>
  <window>
    <template v-slot:component>
      <UserFields @onSubmit="onSubmit">
        <template v-slot:submitters>
          <Button type="submit" :text="text.windows.add" />
        </template>
      </UserFields>
    </template>
  </window>
</template>

<script>
import { mapActions } from "vuex";
import Button from "../../components/Button.vue";
import UserFields from "../../components/users/UserFields.vue";
import window from "../../components/window.vue";
import Ar from "../../lang/ar";

export default {
  components: { window, UserFields, Button },
  name: "AddUser",
  data() {
    return {
      text: Ar,
    };
  },
  methods: {
    // TODO: Add delete edit user
    ...mapActions(["addUser"]),
    async onSubmit(e) {
      try {
        const result = await this.addUser(e);
        if (result.status === 200) {
          this.$toast.success(this.text.messages.success);
          this.$router.push({ name: "users" });
        } else if (result.status === 403) {
          this.$toast.error(this.text.messages.entryError);
        } else {
          this.$toast.error(this.text.messages.error);
        }
      } catch (error) {
        this.$toast.error(this.text.messages.error);
      }
    },
  },
};
</script>

