<template>
  <div class="welcome-body">
    <h2>
      Welcome {{ $store.getters.user.username || $store.getters.user.fullName }}
    </h2>
    <p>{{ timer }}</p>
  </div>
</template>

<script>
export default {
  name: "welcome",
  data() {
    return {
      timer: 5,
    };
  },
  created() {
    setInterval(() => {
      this.timer--;
    }, 1000);
    setTimeout(() => {
      this.$router.push({ name: "Home" });
    }, this.timer * 1000);
  },
};
</script>

