const constants = require("./constants");

const tokensControl = {};

tokensControl.get = function() {
  return JSON.parse(localStorage.getItem(constants.TOKENS_NAME));
};

tokensControl.set = function(tokens) {
  localStorage.setItem(constants.TOKENS_NAME, JSON.stringify(tokens));
};

tokensControl.remove = function() {
  localStorage.removeItem(constants.TOKENS_NAME);
};

tokensControl.check = function() {
  if (localStorage.getItem(constants.TOKENS_NAME) !== null) return true;
  return false;
};

tokensControl.replace = function(tokens) {
  tokensControl.remove();
  localStorage.setItem(constants.TOKENS_NAME, JSON.stringify(tokens));
};

tokensControl.httpAuthorization = new headers(tokensControl);

function headers(tokensControl) {
  const option = (token) => {
    return {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    };
  };
  this.withAccessToken = () => {
    return option(tokensControl.get().x_access);
  };
  this.withRefreshToken = () => {
    return option(tokensControl.get().x_refresh);
  };
}

module.exports = tokensControl;
