<template>
  <PopupWindow
    name="ddww"
    title="Add User"
    v-if="showWindow"
    @closeWin="closeWindow"
  >
    <template v-slot:component>
      <UserFields :userInfo="selectedUser" />
    </template>

    <template v-slot:bottomBarBtns>
      <Button
        @onClick="saveChanges"
        :color="green"
        :icon="save"
        :text="text.windows.save"
      />
    </template>
  </PopupWindow>

  <UsersList
    :users="allUsers"
    @click-edit="openWindow"
    @click:add="$router.push({ name: 'addUser' })"
  />
</template>

<script>
import PopupWindow from "../../components/PopupWindow.vue";
import UserFields from "../../components/users/UserFields.vue";

import UsersList from "../../components/users/list/UsersList.vue";
import Button from "../../components/Button.vue";
import Ar from "../../lang/ar";
import { mapActions, mapGetters } from "vuex";

export default {
  name: "UsersControl",
  data() {
    return {
      text: Ar,
      showWindow: false,
      selectedUser: {},
    };
  },
  created() {
    this.getUsers();
  },
  computed: {
    ...mapGetters(["allUsers"]),
  },
  components: { UsersList, UserFields, PopupWindow, Button },
  methods: {
    ...mapActions(["getUsers"]),
    async saveChanges() {
      this.getUsers();
    },
    closeWindow() {
      this.showWindow = false;
    },
    openWindow(e) {
      this.selectedUser = e;
      this.showWindow = true;
    },
    showEditWindow() {
      console.log("ddd");
    },
  },
};
</script>

