<template>
  <div class="results">
    <Cards :contacts="contacts" @contactsDetails="showDetails" />
    <Pagination
      v-show="contacts.length"
      @extendResults="$emit('extendTheResults')"
      :thereMoreResults="thereMoreResults"
    />
    <NoResult v-show="contacts.length === 0" />
  </div>
  <Review
    :contact="contact"
    :customClass="[showDetailsWin ? 'showUpEffect' : '']"
    @closeWin="closeWin"
    @closeWinDb="closeWinDb"
    v-if="showDetailsWin"
  />
</template>

<script>
import Cards from "../../components/Cards.vue";
import NoResult from "../errors/NoResult.vue";
import Review from "./Review.vue";
import Ar from "../../lang/ar";
import Pagination from "../../components/Pagination.vue";

export default {
  name: "Results",
  emits: ["extendTheResults"],
  components: {
    Cards,
    NoResult,
    Review,
    Pagination,
  },
  props: {
    contacts: Array,
    thereMoreResults: Boolean,
    extendSearchResults: Function,
  },
  data() {
    return {
      text: Ar,
      showDetailsWin: false,
      contact: Object,
    };
  },
  methods: {
    showDetails(e) {
      this.contact = e;
      this.showDetailsWin = true;
    },
    closeWin() {
      this.showDetailsWin = false;
    },
    closeWinDb(e) {
      if (e.target == e.currentTarget) this.showDetailsWin = false;
    },
  },
};
</script>

