<template>
  <Window v-show="!proccessing">
    <template v-slot:component>
      <LoginForm
        :proccessing="proccessing"
        :title="text.windows.titles.login"
        @user="login"
      />
    </template>
  </Window>
  <Spinner v-show="proccessing" />
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import LoginForm from "../../components/users/LoginForm.vue";

import Spinner from "../../components/Spinner.vue";
import Window from "../../components/window.vue";
import Ar from "../../lang/ar";

export default {
  name: "login",
  data() {
    return {
      text: Ar,
      proccessing: false,
      title: "",
    };
  },
  components: {
    Spinner,
    Window,
    LoginForm,
  },
  created() {
    this.title = this.text.windows.titles.login;
  },
  computed: { ...mapGetters(["user"]) },
  methods: {
    ...mapActions(["act_login"]),
    async login(e) {
      try {
        this.proccessing = true;
        const status = await this.act_login(e, this.$toast);
        if (status === 200) {
          this.$router.push({ name: "Welcome" });
          this.$toast.success(this.text.messages.welcome);
        } else if (status === 401) {
          this.$toast.error(this.text.messages.loginInfoError);
        } else {
          this.$toast.error(this.text.messages.error);
        }
        this.proccessing = false;
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>

 <style lang="scss" scoped>
.login-window {
  background-color: $bg-light;
  padding: 10px 40px 20px;
  border-radius: 5px;
}
</style>