<template>
  <div class="card" @dblclick="$emit('contactsDetails', contact)">
    <div class="header"></div>
    <div class="body">
      <img src="../assets/profile.svg" alt="profile" />
      <h3>{{ contactName() }}</h3>
      <p v-if="contact.department">{{ contact.department }}</p>
      <p class="hintText" v-else>{{ text.windows.empty }}</p>
      <p v-if="contact.ipTelephone || contact.mobile">
        {{ contact.ipTelephone ? contact.ipTelephone : contact.mobile }}
      </p>
      <p class="hintText" v-else>{{ text.windows.empty }}</p>
    </div>
  </div>
</template>

<script>
import Ar from "../lang/ar";
export default {
  name: "Card",
  props: {
    contact: Object,
  },
  data() {
    return {
      text: Ar,
      contactName: () => {
        const items = this.contact.name.split(" ");
        const newName = `${items[0]} ${items[items.length - 1]}`;
        return newName;
      },
    };
  },
};
</script>

<style lang="scss" scoped>
.card {
  margin: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  min-width: 300px;
  border-radius: 25px;
  //#2c394b
  background-color: $bg-light;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.5s ease-out;
  transform: scale(100%);
  text-align: center;
  &:hover {
    transition: all 0.5s ease-out;
    transform: scale(105%);
  }
  &:active {
    filter: brightness(50%);
  }
  .body {
    padding: 10px;
    color: #eeeeee;
    line-height: 0.5rem;
    .hintText {
      font-style: italic;
      color: $text-dark;
    }
    img {
      width: 50px;
      height: 46px;
      border-radius: 50%;
      background-color: #1c2633;
      margin-top: 20px;
      transition: all 0.5s ease-out;
      &:hover {
        background-color: #101824;
      }
    }
  }
  .header {
    width: 100%;
    height: 10px;
    background-color: #ff4c29;
  }
}
</style>