import Home from "../../views/main/Home.vue";
import NotFound from "../../views/errors/NotFound.vue";

const main = [
  {
    path: "/",
    name: "Home",
    component: Home,
    meta: {
      requiresAuth: true,
    },
  },
  { path: "/:catchAll(.*)", component: NotFound },
];

export default main;
