<template>
  <contact-fields
    type="view"
    :disabled="disabled"
    :name="name"
    @onSubmit="onSubmit"
  >
    <template v-slot:submitters>
      <Spinner v-show="savingProcess" />
      <div class="btnsBar">
        <Button
          color="green"
          :text="text.windows.add"
          :title="text.windows.add"
          name="add"
        ></Button>
      </div>
    </template>
  </contact-fields>
</template>

<script>
import ContactFields from "./ContactFields.vue";
import Button from "./Button.vue";
import Ar from "../lang/ar";
import Spinner from "./Spinner.vue";
import { mapActions } from "vuex";
export default {
  name: "AddContactForm",
  props: {
    name: String,
  },
  data() {
    return {
      disabled: 0,
      savingProcess: false,
      text: Ar,
    };
  },
  components: {
    ContactFields,
    Button,
    Spinner,
  },
  methods: {
    ...mapActions(["act_addContact"]),
    onSubmit(e) {
      this.add(e.updContact);
    },

    add(contact) {
      this.savingProcess = true;
      this.act_addContact(contact)
        .then(() => {
          this.savingProcess = false;
          this.disabled = 1;
          this.$router.go("-1");
          this.$toast.success(this.text.messages.add_success);
        })
        .catch((e) => {
          this.$toast.error(this.text.messages.error);
          console.error(e.message);
        });
    },
  },
};
</script>

<style lang='scss' scoped>
.icon {
  cursor: pointer;
  color: $text-dark;
  transition: all 0.5s ease-in;
  &:hover {
    filter: brightness(80%);
  }
}

.btnsBar {
  @include flexed(row, space-between, center);
  button:last-child {
    width: 50px;
  }
  button:first-child {
    width: 100%;
  }
}
</style>