import axios from "axios";
import tokensControl from "../../helpers/tokensControl";

const URL = process.env.VUE_APP_SERVICE_URL;

const state = {
  users: [],
  usersLoading: false,
};

const getters = {
  allUsers: (state) => state.users,
  selectUser: (state, id) => state.users._id === id,
  usersLoading: (state) => state.usersLoading,
};

const actions = {
  // gets requests
  async getUsers({ commit }) {
    const results = await axios.get(
      `${URL}/users/control`,
      tokensControl.httpAuthorization.withAccessToken()
    );
    results.data;
    commit("getUsers", results.data);
  },
  // add request
  async addUser({ commit }, user) {
    try {
      commit("usersLoading", true);
      const results = await axios.post(
        `${URL}/users/control`,
        user,
        tokensControl.httpAuthorization.withAccessToken()
      );
      results.data;
      commit("addUser", results.data);
      return { status: results.status };
    } catch (error) {
      console.log(error);
      return { status: error.response.status };
    } finally {
      commit("usersLoading", false);
    }
  },
  // update request
  async updateUser({ commit }, user) {
    const results = await axios.patch(
      `${URL}/users/control`,
      user,
      tokensControl.httpAuthorization.withAccessToken()
    );
    results.data;
    commit("updateUser", results.data);
  },
  async deleteUser({ commit }, id) {
    const results = await axios.delete(
      `${URL}/users/control`,
      id,
      tokensControl.httpAuthorization.withAccessToken()
    );
    results.data;
    commit("deleteUser", results.data);
  },
};

const mutations = {
  usersLoading: (state, data) => (state.usersLoading = data),
  getUsers: (state, users) => (state.users = users),
  addUser: (state, user) => state.users.push(user),
  deleteUser: (state, id) =>
    (state.users = state.users.filter((item) => item.id !== id)),
  updateUser: (state, user) => {
    const index = state.users.findIndex((item) => item.id === user.id);
    if (index !== -1) state.users.splice(index, 1, user);
  },
};

export default {
  state,
  getters,
  actions,
  mutations,
};
