<template>
  <form @submit.prevent="onsubmit">
    <div class="form-group">
      <img
        class="profilePhoto"
        src="@/assets/person.png"
        :alt="updContact.name"
      />

      <div class="container">
        <div class="info-fields">
          <Input
            v-model="updContact.name"
            :name="name"
            :label="ar.user.name"
            :edit="disabled"
            :required="true"
          />
          <Input
            v-model="updContact.department"
            :name="name"
            :label="ar.user.department"
            :edit="disabled"
          />
          <Input
            v-model="updContact.position"
            :name="name"
            :label="ar.user.position"
            :edit="disabled"
          />
          <Input
            v-model="updContact.telephone"
            :name="name"
            :label="ar.user.telephone"
            :edit="disabled"
          />
          <Input
            v-model="updContact.ipTelephone"
            :name="name"
            :label="ar.user.ipTelephone"
            :edit="disabled"
          />
          <Input
            v-model="updContact.mobile"
            :name="name"
            :label="ar.user.mobile"
            :edit="disabled"
          />
          <Input
            v-model="updContact.email"
            type="email"
            :name="name"
            :label="ar.user.email"
            :edit="disabled"
          />
        </div>
      </div>
    </div>
    <Input
      tag="textarea"
      v-model="updContact.note"
      :name="name"
      :label="ar.user.note"
      :edit="disabled"
    />
    <slot name="submitters"></slot>
  </form>
</template>


<script>
import Input from "./Input.vue";
import ar from "../lang/ar";

export default {
  name: "ContactFields",
  props: {
    contact: Object,
    name: String,
    title: String,
    disabled: Number,
    type: { type: String, required: true },
  },
  data() {
    return {
      ar,
      updContact: { ...this.contact },
    };
  },
  mounted() {
    this.$emit("reset", this.reset);
  },
  components: {
    Input,
  },
  methods: {
    reset() {
      this.updContact = this.contact;
    },
    onsubmit(e) {
      this.$emit("onSubmit", {
        updContact: this.updContact,
        submitter: e.submitter.name,
      });
    },
  },
};
</script>

<style lang='scss'>
.form-group {
  // border-top: 1px solid $line;
  color: $text-dark;
  position: relative;
  @include flexed(column, space-between, flex-start);
  .profilePhoto {
    background-color: $bg-light;
    border-radius: 5px;
    max-width: 190px;
  }

  padding: 10px;
  .container {
    text-align: right;

    padding: 10px;
    @include flexed(row, space-between, flex-start);
    .info-fields {
      @include flexed(row, space-between, center);
      @media only screen and (max-width: 700px) {
        @include flexed(column, space-between, center);
        flex-wrap: wrap;
      }
      flex-wrap: wrap;
      .input-group {
        width: 30%;
        @media only screen and (max-width: 700px) {
          width: 100%;
        }
      }
    }
  }
}
</style>