export default function Language(name) {
  this.name = name;
  this.user = {
    name: String,
    administration: { type: String, required: true },
    department: String,
    position: String,
    note: String,
    telephone: String,
    ipTelephone: String,
    fax: String,
    mobile: String,
    email: String,
    //users pages
    username: String,
    password: String,
    firstName: String,
    lastName: String,
  };
  this.windows = {
    close: String,
    edit_contact: String,
    undo: String,
    save_changes: String,
    save: String,
    edit: String,
    add: String,
    delete: String,
    empty: String,
    areYouSureEdit: String,
    footerText: String,
    changePassword: String,
    oldPassword: String,
    newPassword: String,
    newPasswordConfirm: String,
    cancel:String,

    //titles
    titles: {
      contact_info: String,
      add_contact: String,
      usersList: String,
      //users pages
      login: String,
      logout: String,
      register: String,

      //Profile

      profile: String,
    },
  };
  this.messages = {
    welcome: String,
    search: String,
    results: String,
    noMoreResults: String,
    add_success: String,
    edit_success: String,
    delete_success: String,
    error: String,
    entryError: String,
    success: String,

    loginInfoError: String,
    loggedOut: String,
    totalOfUsers: String,
    newPasswordConfirmNotSame: String,
    oldPasswordSameNew: String,
    passwordError: String,
  };
}
