import axios from "axios";
import tokensControl from "../../helpers/tokensControl";

const URL = process.env.VUE_APP_SERVICE_URL;

const state = {
  profile: {},
  porfileIsLoading: false,
};

const getters = {
  getProfile: (state) => state.profile,
  porfileIsLoading: (state) => state.porfileIsLoading,
};

const actions = {
  // gets requests
  async requestProfile({ commit }) {
    try {
      commit("porfileIsLoading", true);
      const results = await axios.get(
        `${URL}/profile`,
        tokensControl.httpAuthorization.withAccessToken()
      );
      commit("getProfile", results.data);
    } catch (error) {
      console.log(error);
    } finally {
      commit("porfileIsLoading", false);
    }
  },

  async update({ commit }, data) {
    try {
      commit("porfileIsLoading", true);
      const results = await axios.patch(
        `${URL}/profile`,
        data,
        tokensControl.httpAuthorization.withAccessToken()
      );

      commit("getProfile", results.data);
      return true;
    } catch (error) {
      console.log(error);
      return false;
    } finally {
      commit("porfileIsLoading", false);
    }
  },

  async changePassword(s, data) {
    try {
      await axios.patch(
        `${URL}/profile/changePassword`,
        data,
        tokensControl.httpAuthorization.withAccessToken()
      );

      return [true, 200];
    } catch (error) {
      if (error.response.status === 401) return [false, 401];
      return [false, 500];
    }
  },
};

const mutations = {
  getProfile: (state, profile) => (state.profile = profile),
  porfileIsLoading: (state, data) => (state.porfileIsLoading = data),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
