<template>
  <div class="pagination">
    <Button
      v-show="thereMoreResults"
      @click="$emit('extendResults')"
      :type="'button'"
      :text="'more'"
      icon="arrow-circle-down"
    />
    <div v-show="!thereMoreResults" class="noMoreMessage">
      {{ text.messages.noMoreResults }}
    </div>
  </div>
</template>

<script>
import Ar from "../lang/ar";
import Button from "./Button.vue";

export default {
  components: { Button },
  name: "Pagination",
  props: {
    thereMoreResults: Boolean,
  },
  data() {
    return {
      text: Ar,
    };
  },
  component: Button,
};
</script>

<style lang="scss" scoped>
.pagination {
  @include flexed(row, center, center);
  min-height: 10vh;
  background-color: $bg-dark;
  .noMoreMessage {
    color: $text_dark;
  }
  button {
    width: 30vw;
  }
}
</style>