<template>
  <div class="searchbox">
    <form @submit="onsubmit">
      <label for="search"> {{ text.messages.search }}</label>
      <div class="search-form">
        <Input v-model="searchText" name="search" placeholder="البحث" />
        <Button type="submit" icon="search" color="green" />
      </div>
    </form>
  </div>
</template>

<script>
import Ar from "../lang/ar";
import Button from "./Button.vue";
import Input from "./Input.vue";

export default {
  name: "SearchBox",
  components: {
    Input,
    Button,
  },
  data() {
    return {
      text: Ar,
      searchText: "",
    };
  },
  props: {
    label: String,
  },

  methods: {
    onsubmit(e) {
      e.preventDefault();

      this.$router.push({ name: "Search", query: { q: this.searchText } });
      this.$emit("search", this.searchText || this.$route.query.q);
    },
  },
};
</script>
<style lang="scss" scoped>
.searchbox {
  width: 600px;
  @media only screen and (max-width: $small-screen) {
    width: 80%;
  }
  text-align: right;
  .search-form {
    display: grid;
    grid-template-columns: 80% 20%;
    grid-gap: 10px;
  }
  label {
    font-size: 1.2rem;
    margin-bottom: 20px;
  }
}
</style>