<template>
  <div class="input-group">
    <label :for="name">{{ label }}</label>

    <input
      v-if="tag != 'textarea'"
      :class="[edit ? 'input-light' : 'input-dark ', className]"
      :type="type"
      :name="name"
      :value="modelValue"
      :placeholder="placeholder"
      :disabled="edit"
      autocomplete="off"
      @input="$emit('update:modelValue', $event.target.value)"
      :required="required"
    />
    <textarea
      v-if="tag === 'textarea'"
      :class="[edit ? 'input-light' : 'input-dark ', className]"
      :type="type"
      :name="name"
      :value="modelValue"
      :placeholder="placeholder"
      :disabled="edit"
      @input="$emit('update:modelValue', $event.target.value)"
    />
  </div>
</template>

<script>
export default {
  name: "Input",
  props: {
    className: { String },
    type: { type: String, default: "text" },
    required: { type: Boolean, default: false },
    name: String,
    label: String,
    placeholder: String,
    modelValue: String,
    tag: String,
    edit: { type: Number, default: 0 },
  },
};
</script>

<style lang="scss" scoped>
.input-group {
  width: 100%;
  @include flexed(column, space-between, flex-start) label {
    color: $text-light;
  }
  input[type="text"],
  input[type="email"],
  input[type="search"],
  input[type="password"],
  textarea {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid $line;

    border-radius: 4px;
    box-sizing: border-box;
    resize: none;
    color: $text-dark;
    &:focus {
      outline: none;
      box-shadow: 0 0 0 1pt grey;
    }
  }
  .input-light {
    background-color: $bg-light;
  }
  .input-dark {
    background-color: $bg-dark;
  }
}
</style>