import usersControl from "../../views/users/UsersControl";
import AddUser from "../../views/users/AddUser";

const users = [
  {
    path: "/users/control",
    name: "users",
    component: usersControl,
  },
  {
    path: "/users/add",
    name: "addUser",
    component: AddUser,
  },
];

export default users;
