<template>
  <div class="profile">
    <div class="profile-head">
      <h2>{{ text.windows.titles.profile }}</h2>
      <Icon @click="editMood" :hint="text.windows.edit" icon="edit" />
    </div>
    <Spinner v-if="porfileIsLoading" />
    <window v-else>
      <template v-slot:component>
        <div class="profile-body">
          <div class="profile-contacts"></div>
          <div class="profile-userInfo">
            <form @submit.prevent="change">
              <Input
                :label="text.user.firstName"
                :edit="notActive"
                v-model="getProfile.firstName"
              />

              <Input
                :label="text.user.lastName"
                :edit="notActive"
                v-model="getProfile.lastName"
              />

              <Input
                :label="text.user.email"
                :edit="notActive"
                v-model="getProfile.email"
              />

              <Input
                :label="text.user.telephone"
                :edit="notActive"
                v-model="getProfile.phone"
              />
              <router-link to="/profile/changePassword">
                {{ text.windows.changePassword }}
              </router-link>
              <Button
                type="submit"
                @Click="saveChanges"
                :text="text.windows.save"
              />
              <ButtonCancel />
            </form>
          </div>
        </div>
      </template>
    </window>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

import Button from "../../components/Button.vue";
import Icon from "../../components/Icon.vue";
import Input from "../../components/Input.vue";
import window from "../../components/window.vue";
import Ar from "../../lang/ar";

import getProfileFields from "../../helpers/constraction/profile";
import Spinner from "../../components/Spinner.vue";
import ButtonCancel from "../../components/ButtonCancel.vue";

export default {
  components: { window, Input, Icon, Button, Spinner, ButtonCancel },
  name: "Profile",
  data() {
    return {
      text: Ar,
      notActive: 1,
    };
  },
  computed: {
    ...mapGetters(["getProfile", "porfileIsLoading"]),
  },
  methods: {
    ...mapActions(["requestProfile", "update"]),

    editMood() {
      if (this.notActive === 1) return (this.notActive = 0);

      this.notActive = 1;
    },
    async saveChanges() {
      const fields = getProfileFields(this.getProfile);

      const result = await this.update(fields);

      location.reload();

      if (!result) return this.$toast.error(this.text.messages.error);

      this.$toast.success(this.text.messages.edit_success);

      this.requestProfile();
    },
  },
  created() {
    this.requestProfile();
  },
};
</script>

<style lang="scss" scoped>
.profile {
  width: 100%;
  @media only screen and (max-width: $small-screen) {
    width: 90%;
  }
  .profile-head {
    @include flexed(row, space-between, center);
  }
  .profile-body {
    min-width: 40vw;
    .profile-userInfo {
      @include flexed(column, space-between, flex-start);
      .row {
        width: 100%;
        @include flexed(row, space-between, center);
        > div {
          margin: 0 0 0 9px;
        }
      }
    }
  }
}
</style>